body{margin:0;font-family:'PT Sans', sans-serif}.MuiTableCell-root{font-family:'PT Sans', sans-serif !important}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.mennu-item-paper{overflow:hidden}.mennu-item-paper:hover{cursor:pointer}.mennu-item-paper:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.mennu-item-paper:hover .content{opacity:0.9}



.image-container{width:200px}.image-container img{border-radius:4px;width:100%;height:100%}


.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:40px;height:40px}.cart-icon .item-count{position:absolute;font-size:12px;font-weight:bold;bottom:10px;-webkit-user-select:none;-ms-user-select:none;user-select:none}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px;color:#000}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:auto}.cart-dropdown button{margin-top:auto}.empty-message{font-size:18px;margin:50px auto;color:black}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px}.header .logo-container{height:100%;width:70px;padding:25px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px}

a{text-decoration:none}

